
import { defineComponent } from 'vue'
import * as API from '@/API'

export default defineComponent({
    data() {
        return {
            confirmed: '',
            password: '',
            email: '',
            error: '',
            registering: false,
        }
    },
    methods: {
        async register() {
            if (this.confirmed !== this.password) this.error = 'Die passwrter stimmen nicht berein'
            this.registering = true
            try {
                await API.register(this.email, this.password)
                this.$router.push('/game')
            } catch (e) {
                console.log("couldn't register", e)
                this.error = 'Der Account konnte leider nicht registriert werden'
            } finally {
                this.registering = false
            }
        },
    },
})
